body {
  margin: 0;
  padding: 0;
  overflow: hidden; /* Hide native scrollbars */
  -webkit-app-region: no-drag; /* Allow interaction with elements */
  font-family: "Segoe UI", "Roboto", "Arial", sans-serif; /* Windows 11 native font */
  background: #0f0f0f;
  animation: fadeIn 0.5s ease-in-out;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes fadeOut {
  from { opacity: 1; }
  to { opacity: 0; }
}

@keyframes videoTransition {
  0% { 
    opacity: 0; 
    transform: scale(0.95) translateY(10px);
  }
  100% { 
    opacity: 1; 
    transform: scale(1) translateY(0);
  }
}

@keyframes playerSectionTransition {
  0% { 
    opacity: 0; 
    transform: translateX(-20px);
  }
  100% { 
    opacity: 1; 
    transform: translateX(0);
  }
}

@keyframes slideInFromLeft {
  from { 
    opacity: 0;
    transform: translateX(-30px);
  }
  to { 
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes slideInFromRight {
  from { 
    opacity: 0;
    transform: translateX(30px);
  }
  to { 
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes slideInFromBottom {
  from { 
    opacity: 0;
    transform: translateY(20px);
  }
  to { 
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes pulse {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.05); }
}

@keyframes shimmer {
  0% { background-position: -468px 0; }
  100% { background-position: 468px 0; }
}

/* Settings Panel Styles */
.settings-btn {
  background: none;
  border: none;
  color: #b3b3b3;
  font-size: 16px;
  cursor: pointer;
  padding: 8px;
  border-radius: 6px;
  transition: all 0.2s;
  margin-right: 10px;
}

.settings-btn:hover {
  background: rgba(255, 255, 255, 0.1);
  color: white;
  transform: rotate(90deg);
}

.settings-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: center;
  animation: fadeIn 0.3s ease-out;
}

.settings-panel {
  background: linear-gradient(135deg, #1a1a1a, #2a2a2a);
  border-radius: 16px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  width: 90%;
  max-width: 800px;
  max-height: 90vh;
  overflow: hidden;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.5);
  animation: slideInFromBottom 0.3s ease-out;
}

.settings-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px 25px;
  background: rgba(0, 122, 204, 0.1);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.settings-header h2 {
  margin: 0;
  color: #007acc;
  font-size: 18px;
  font-weight: 600;
}

.close-settings-btn {
  background: none;
  border: none;
  color: #b3b3b3;
  font-size: 24px;
  cursor: pointer;
  padding: 5px 10px;
  border-radius: 6px;
  transition: all 0.2s;
}

.close-settings-btn:hover {
  background: rgba(255, 255, 255, 0.1);
  color: white;
}

.settings-content {
  max-height: 70vh;
  overflow-y: auto;
  padding: 0 25px;
}

.settings-section {
  margin: 25px 0;
  padding-bottom: 20px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
}

.settings-section:last-child {
  border-bottom: none;
}

.settings-section h3 {
  margin: 0 0 15px 0;
  color: white;
  font-size: 16px;
  font-weight: 600;
}

.setting-item {
  margin: 15px 0;
}

.setting-item label {
  display: flex;
  align-items: center;
  gap: 8px;
  color: #e0e0e0;
  font-size: 14px;
  cursor: pointer;
}

.setting-item input[type="checkbox"] {
  accent-color: #007acc;
  width: 16px;
  height: 16px;
}

.setting-description {
  margin: 5px 0 0 24px;
  font-size: 12px;
  color: #888;
  font-style: italic;
}

.setting-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 10px;
}

.setting-row h4 {
  margin: 0;
  color: #e0e0e0;
  font-size: 14px;
}

.add-folder-btn {
  padding: 6px 12px;
  background: linear-gradient(135deg, #007acc, #005a9e);
  border: none;
  border-radius: 6px;
  color: white;
  cursor: pointer;
  font-size: 12px;
  transition: all 0.2s;
}

.add-folder-btn:hover {
  background: linear-gradient(135deg, #008ce0, #0066b3);
  transform: translateY(-1px);
}

.watched-folders-list {
  max-height: 200px;
  overflow-y: auto;
  background: rgba(255, 255, 255, 0.02);
  border-radius: 8px;
  padding: 10px;
}

.no-folders {
  text-align: center;
  color: #888;
  font-style: italic;
  padding: 20px;
  margin: 0;
}

.folder-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 8px 12px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 6px;
  margin-bottom: 8px;
  border: 1px solid rgba(255, 255, 255, 0.05);
}

.folder-info {
  flex: 1;
  min-width: 0;
}

.folder-path {
  display: block;
  color: #e0e0e0;
  font-size: 12px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 400px;
}

.folder-stats {
  font-size: 10px;
  color: #888;
  margin-top: 2px;
}

.folder-actions {
  display: flex;
  gap: 5px;
}

.visibility-btn {
  background: none;
  border: none;
  font-size: 14px;
  cursor: pointer;
  padding: 4px;
  border-radius: 4px;
  transition: all 0.2s;
}

.visibility-btn:hover {
  background: rgba(255, 255, 255, 0.1);
}

.visibility-btn.hidden {
  opacity: 0.5;
}

.remove-folder-btn {
  background: none;
  border: none;
  color: #ff6b6b;
  font-size: 12px;
  cursor: pointer;
  padding: 4px;
  border-radius: 4px;
  transition: all 0.2s;
}

.remove-folder-btn:hover {
  background: rgba(255, 107, 107, 0.2);
}

.range-input-group {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-top: 5px;
}

.volume-range,
.thumbnail-range {
  flex: 1;
  height: 4px;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 2px;
  outline: none;
  -webkit-appearance: none;
  appearance: none;
}

.volume-range::-webkit-slider-thumb,
.thumbnail-range::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 12px;
  height: 12px;
  background: #007acc;
  border-radius: 50%;
  cursor: pointer;
}

.speed-select,
.theme-select {
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 6px;
  color: white;
  padding: 6px 10px;
  font-size: 12px;
  margin-top: 5px;
  width: 150px;
}

.speed-select:focus,
.theme-select:focus {
  outline: none;
  border-color: #007acc;
  box-shadow: 0 0 0 2px rgba(0, 122, 204, 0.3);
}

.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  gap: 15px;
  margin-top: 10px;
}

.stat-item {
  text-align: center;
  padding: 15px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 8px;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.stat-value {
  font-size: 24px;
  font-weight: bold;
  color: #007acc;
  margin-bottom: 5px;
}

.stat-label {
  font-size: 11px;
  color: #888;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.shortcuts-list {
  display: grid;
  gap: 8px;
}

.shortcut-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px 12px;
  background: rgba(255, 255, 255, 0.02);
  border-radius: 6px;
  font-size: 12px;
}

.shortcut-item kbd {
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 4px;
  padding: 2px 6px;
  font-family: monospace;
  font-size: 10px;
  color: #007acc;
}

.settings-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 25px;
  background: rgba(255, 255, 255, 0.02);
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

.reset-btn {
  padding: 8px 16px;
  background: linear-gradient(135deg, #ff6b6b, #ee5a52);
  border: none;
  border-radius: 6px;
  color: white;
  cursor: pointer;
  font-size: 12px;
  transition: all 0.2s;
}

.reset-btn:hover {
  background: linear-gradient(135deg, #ff5252, #e53935);
  transform: translateY(-1px);
}

.done-btn {
  padding: 8px 16px;
  background: linear-gradient(135deg, #4caf50, #45a049);
  border: none;
  border-radius: 6px;
  color: white;
  cursor: pointer;
  font-size: 12px;
  transition: all 0.2s;
}

.done-btn:hover {
  background: linear-gradient(135deg, #66bb6a, #4caf50);
  transform: translateY(-1px);
}

/* Scrollbar for settings content */
.settings-content::-webkit-scrollbar {
  width: 6px;
}

.settings-content::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 3px;
}

.settings-content::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, 0.2);
  border-radius: 3px;
}

.settings-content::-webkit-scrollbar-thumb:hover {
  background: rgba(255, 255, 255, 0.3);
}

/* Responsive settings panel */
@media (max-width: 768px) {
  .settings-panel {
    width: 95%;
    margin: 20px;
  }
  
  .settings-header,
  .settings-footer {
    padding: 15px 20px;
  }
  
  .settings-content {
    padding: 0 20px;
  }
  
  .stats-grid {
    grid-template-columns: repeat(2, 1fr);
  }
  
  .folder-path {
    max-width: 200px;
  }
}

/* Enhanced Loading and Empty States */
.loading-container {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 400px;
  padding: 60px 20px;
}

.loading-content {
  text-align: center;
  color: white;
}

.loading-spinner-large {
  width: 60px;
  height: 60px;
  border: 4px solid rgba(255, 255, 255, 0.1);
  border-left: 4px solid #007acc;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin: 0 auto 20px auto;
}

.loading-content h3 {
  margin: 0 0 8px 0;
  font-size: 20px;
  font-weight: 600;
  color: #007acc;
}

.loading-content p {
  margin: 0;
  font-size: 14px;
  color: #b3b3b3;
}

.empty-state-enhanced {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 80px 40px;
  max-width: 800px;
  margin: 0 auto;
  min-height: 500px;
}

.empty-state-enhanced .empty-state-icon {
  font-size: 80px;
  margin-bottom: 25px;
  opacity: 0.8;
  filter: grayscale(0.2);
}

.empty-state-enhanced h3 {
  font-size: 28px;
  font-weight: 700;
  color: #ffffff;
  margin: 0 0 15px 0;
  background: linear-gradient(135deg, #ffffff 0%, #007acc 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.empty-state-enhanced p {
  font-size: 16px;
  color: #b3b3b3;
  margin: 0 0 40px 0;
  line-height: 1.5;
}

.empty-state-suggestions {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 25px;
  width: 100%;
  max-width: 720px;
}

.suggestion-card {
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.08), rgba(255, 255, 255, 0.04));
  border: 1px solid rgba(255, 255, 255, 0.12);
  border-radius: 16px;
  padding: 30px 20px;
  text-align: center;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
}

.suggestion-card:hover {
  transform: translateY(-4px);
  border-color: rgba(0, 122, 204, 0.3);
  box-shadow: 0 12px 32px rgba(0, 122, 204, 0.15);
}

.suggestion-icon {
  font-size: 40px;
  margin-bottom: 15px;
  opacity: 0.9;
}

.suggestion-card h4 {
  font-size: 18px;
  font-weight: 600;
  color: #ffffff;
  margin: 0 0 8px 0;
}

.suggestion-card p {
  font-size: 13px;
  color: #b3b3b3;
  margin: 0 0 20px 0;
  line-height: 1.4;
}

.suggestion-btn {
  padding: 10px 20px;
  border: none;
  border-radius: 8px;
  font-size: 13px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
  width: 100%;
}

.suggestion-btn {
  background: linear-gradient(135deg, #007acc, #0096ff);
  color: white;
}

.suggestion-btn:hover {
  background: linear-gradient(135deg, #0096ff, #00aaff);
  transform: translateY(-1px);
}

.suggestion-btn.secondary {
  background: linear-gradient(135deg, #4caf50, #66bb6a);
}

.suggestion-btn.secondary:hover {
  background: linear-gradient(135deg, #66bb6a, #81c784);
}

.suggestion-btn.tertiary {
  background: linear-gradient(135deg, #ff9800, #ffb74d);
}

.suggestion-btn.tertiary:hover {
  background: linear-gradient(135deg, #ffb74d, #ffcc02);
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
.enhanced-controls {
  display: flex;
  gap: 20px;
  align-items: center;
  justify-content: space-between;
  margin-top: 15px;
  padding: 15px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 12px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.control-group {
  display: flex;
  align-items: center;
  gap: 8px;
}

.control-group label {
  font-size: 12px;
  font-weight: 500;
  color: #b3b3b3;
  min-width: 50px;
}

.speed-control {
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 6px;
  color: white;
  padding: 4px 8px;
  font-size: 12px;
}

.speed-control:focus {
  outline: none;
  border-color: #007acc;
  box-shadow: 0 0 0 2px rgba(0, 122, 204, 0.3);
}

.volume-control {
  width: 80px;
  height: 4px;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 2px;
  outline: none;
  -webkit-appearance: none;
}

.volume-control::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 12px;
  height: 12px;
  background: #007acc;
  border-radius: 50%;
  cursor: pointer;
}

.feature-btn {
  padding: 6px 12px;
  font-size: 11px;
  border-radius: 6px;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  color: white;
  cursor: pointer;
  transition: all 0.2s;
}

.feature-btn:hover {
  background: rgba(255, 255, 255, 0.2);
  transform: translateY(-1px);
}

/* Video Information Panel */
.video-info-panel {
  margin-top: 15px;
  padding: 15px;
  background: rgba(0, 0, 0, 0.7);
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  animation: slideInFromBottom 0.3s ease-out;
}

.video-info-panel h4 {
  margin: 0 0 10px 0;
  color: #007acc;
  font-size: 14px;
}

.info-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 8px;
}

.info-item {
  display: flex;
  justify-content: space-between;
  font-size: 12px;
  color: #b3b3b3;
}

.info-item strong {
  color: white;
}

/* Navigation Controls */
.navigation-controls {
  display: flex;
  align-items: center;
  gap: 15px;
  margin-top: 15px;
  padding: 15px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 12px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.nav-btn {
  padding: 8px 16px;
  background: linear-gradient(135deg, #007acc, #005a9e);
  border: none;
  border-radius: 8px;
  color: white;
  cursor: pointer;
  font-size: 12px;
  font-weight: 500;
  transition: all 0.2s;
}

.nav-btn:hover:not(:disabled) {
  background: linear-gradient(135deg, #008ce0, #0066b3);
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(0, 122, 204, 0.3);
}

.nav-btn:disabled {
  opacity: 0.4;
  cursor: not-allowed;
}

.auto-play-toggle {
  margin-left: auto;
}

.auto-play-toggle label {
  display: flex;
  align-items: center;
  gap: 6px;
  font-size: 12px;
  color: #b3b3b3;
  cursor: pointer;
}

.auto-play-toggle input[type="checkbox"] {
  accent-color: #007acc;
}

/* Favorite Button */
.favorite-btn {
  background: none;
  border: none;
  font-size: 16px;
  cursor: pointer;
  margin-left: 10px;
  padding: 5px;
  border-radius: 50%;
  transition: all 0.2s;
}

.favorite-btn:hover {
  background: rgba(255, 255, 255, 0.1);
  transform: scale(1.1);
}

.favorite-btn.favorited {
  animation: pulse 0.3s ease-in-out;
}

.favorite-btn-small {
  background: none;
  border: none;
  font-size: 14px;
  cursor: pointer;
  padding: 4px;
  border-radius: 4px;
  transition: all 0.2s;
}

.favorite-btn-small:hover {
  background: rgba(255, 255, 255, 0.1);
  transform: scale(1.1);
}

/* Recently Played Section */
.recently-played-section {
  margin-top: 20px;
  background: rgba(255, 255, 255, 0.02);
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.05);
  overflow: hidden;
}

.recently-played-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  cursor: pointer;
  transition: all 0.2s;
  background: rgba(255, 255, 255, 0.02);
  user-select: none;
}

.recently-played-header:hover {
  background: rgba(255, 255, 255, 0.05);
}

.recently-played-header h3 {
  margin: 0;
  color: #007acc;
  font-size: 16px;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 8px;
}

.expand-icon {
  font-size: 12px;
  color: #b3b3b3;
  transition: transform 0.2s ease;
  transform: rotate(0deg);
}

.expand-icon.expanded {
  transform: rotate(90deg);
}

.expand-btn {
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 4px;
  color: #b3b3b3;
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  font-size: 14px;
  font-weight: bold;
  transition: all 0.2s;
}

.expand-btn:hover {
  background: rgba(255, 255, 255, 0.2);
  color: white;
}

.recent-videos-container {
  overflow: hidden;
  transition: max-height 0.3s ease, opacity 0.3s ease;
}

.recent-videos-container.collapsed {
  max-height: 0;
  opacity: 0;
}

.recent-videos-container.expanded {
  max-height: 200px;
  opacity: 1;
}

.recent-videos {
  display: flex;
  gap: 15px;
  overflow-x: auto;
  padding: 0 20px 20px 20px;
  scrollbar-width: thin;
  scrollbar-color: rgba(255, 255, 255, 0.2) transparent;
}

.recent-videos::-webkit-scrollbar {
  height: 4px;
}

.recent-videos::-webkit-scrollbar-track {
  background: transparent;
}

.recent-videos::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, 0.2);
  border-radius: 2px;
}

.recent-videos::-webkit-scrollbar-thumb:hover {
  background: rgba(255, 255, 255, 0.3);
}

.recent-video-item {
  flex-shrink: 0;
  width: 160px;
  cursor: pointer;
  border-radius: 8px;
  overflow: hidden;
  transition: all 0.2s;
  background: rgba(255, 255, 255, 0.05);
}

.recent-video-item:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 20px rgba(0, 122, 204, 0.2);
}

.recent-thumbnail {
  width: 100%;
  height: 90px;
  object-fit: cover;
  border-radius: 8px 8px 0 0;
}

.recent-title {
  padding: 8px;
  font-size: 11px;
  color: #b3b3b3;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

/* Enhanced metadata display */
.metadata-item {
  display: flex;
  align-items: center;
  gap: 4px;
  font-size: 11px;
